import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";
import { MainScene } from "./MainScene";

export class AvatarGenerationLayer extends Phaser.GameObjects.Container {

    private static DELTA_TIME = 45;

    private titleLabel: Phaser.GameObjects.BitmapText;
    private embryo1: Phaser.GameObjects.Image;
    private embryo2: Phaser.GameObjects.Image;
    private embryo3: Phaser.GameObjects.Image;
    private f: number;
    private message1Container: Phaser.GameObjects.Container;
    private isBorn: boolean;
    private embryosLoopPaused: boolean;
    private pack_id: number;
    private scaledItemsContainer: Phaser.GameObjects.Container;
    private embryo3Tween: Phaser.Tweens.Tween;

    constructor(scene: Phaser.Scene, pack_id?: number) {

        super(scene);

        this.f = 0;
        this.isBorn = false;
        this.embryosLoopPaused = false;
        this.pack_id = pack_id || 9999999999;

        this.scaledItemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.scaledItemsContainer.x = GameConstants.GAME_WIDTH / 2;
        this.scaledItemsContainer.y = GameConstants.GAME_HEIGHT / 2;
        this.scaledItemsContainer.scaleY = GameVars.scaleY;
        this.add(this.scaledItemsContainer);

        const w = 700;
        const h = 870;

        const tmpBackground = new Phaser.GameObjects.Graphics(this.scene);
        tmpBackground.fillStyle(0x000000, .925);
        tmpBackground.fillRect(-w / 2, -h / 2 + 20, w, h);
        tmpBackground.setInteractive(new Phaser.Geom.Rectangle(-w / 2, -h / 2 + 20, w, h), Phaser.Geom.Rectangle.Contains);
        this.scaledItemsContainer.add(tmpBackground);

        const frame = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "bg_frame");
        this.scaledItemsContainer.add(frame);

        this.titleLabel = new Phaser.GameObjects.BitmapText(this.scene, 0, -460 * GameVars.scaleY, "eunomia", "BIRTH", 43);
        this.titleLabel.setOrigin(.5);
        this.scaledItemsContainer.add(this.titleLabel);

        this.embryo1 = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "minting_1");
        this.scaledItemsContainer.add(this.embryo1);

        this.embryo2 = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "minting_2");
        this.embryo2.alpha = 0;
        this.scaledItemsContainer.add(this.embryo2);

        this.embryo3 = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "minting_3");
        this.embryo3.alpha = 0;
        this.scaledItemsContainer.add(this.embryo3);

        this.message1Container = new Phaser.GameObjects.Container(this.scene);
        this.message1Container.y = 285;
        this.scaledItemsContainer.add(this.message1Container);

        const label1 = new Phaser.GameObjects.BitmapText(this.scene, 0, 0, "oswald", "PLEASE WAIT WHILE CITIZEN", 43);
        label1.setOrigin(.5);
        label1.tint = 0x82d6f8;
        this.message1Container.add(label1);

        const label2 = new Phaser.GameObjects.BitmapText(this.scene, 0, 50, "oswald", "#" + this.pack_id.toString(), 43);
        label2.setOrigin(.5);
        label2.tint = 0x82d6f8;
        this.message1Container.add(label2);

        const label3 = new Phaser.GameObjects.BitmapText(this.scene, 0, 100, "oswald", "IS BEING BORN", 43);
        label3.setOrigin(.5);
        label3.tint = 0x82d6f8;
        this.message1Container.add(label3);

        this.message1Container.alpha = 0;

        this.scene.tweens.add({
            targets: this.message1Container,
            alpha: 1,
            y: 270,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            delay: 600
        });

        MainScene.currentInstance.humSound.play();
    }

    public update(): void {

        if (this.isBorn ||  this.embryosLoopPaused || !this.scene) {
            return;
        }

        this.f ++;

        if (this.f === AvatarGenerationLayer.DELTA_TIME) {

            this.scene.tweens.add({
                targets: this.embryo1,
                alpha: 0,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 700
            });

            this.embryo2.alpha = 1;

            MainScene.currentInstance.humSound.play();

        } else if (this.f === 2 * AvatarGenerationLayer.DELTA_TIME) {

            this.scene.tweens.add({
                targets: this.embryo2,
                alpha: 0,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 700
            });

            this.embryo3.alpha = 1;

            MainScene.currentInstance.humSound.play();

        } else if (this.f === 3 * AvatarGenerationLayer.DELTA_TIME) {

            this.embryosLoopPaused = true;
           
            this.embryo3Tween = this.scene.tweens.add({
                targets: this.embryo3,
                alpha: 0,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 500,
                onComplete: function(): void {

                    if (!this.scene) {
                        return;
                    }

                    this.scene.time.delayedCall(500, function(): void {

                        this.f = 0;

                        this.embryo1.alpha = 1;
                        this.embryosLoopPaused = false;

                        MainScene.currentInstance.humSound.play();

                    }, [], this);
                },
                onCompleteScope: this
            });
        }
    }

    public onNFTAvailable(nft_id: number): void {

        this.isBorn = true;
        this.embryosLoopPaused = true;

        this.embryo1.visible = false;
        this.embryo2.visible = false;

        this.embryo3.alpha = 1;

        if (this.embryo3Tween)  {
            this.embryo3Tween.remove();
        }
    
        const bornLabel = new Phaser.GameObjects.BitmapText(this.scene, 0, this.message1Container.y + 75, "oswald", "SUBJECT #" + nft_id.toString() + " IS BORN", 43);
        bornLabel.setOrigin(.5);
        bornLabel.tint = 0x82d6f8;
        this.scaledItemsContainer.add(bornLabel);
        
        this.scene.tweens.add({
            targets: this.message1Container,
            alpha: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: function(): void {
                this.message1Container.visible = false;
            },
            onCompleteScope: this
        });

        let atomic_link = "https://wax.atomichub.io";

        if (GameVars.reactClass && GameVars.reactClass.state && GameVars.reactClass.state.serverType === "testnet") {
            atomic_link = "https://wax-test.atomichub.io";
        }

        const atomicabel = new Phaser.GameObjects.BitmapText(this.scene, 0, bornLabel.y + 60, "oswald", "VIEW ON ATOMIC", 43);
        atomicabel.setOrigin(.5);
        atomicabel.tint = 0xea923e;
        atomicabel.setInteractive({cursor: "pointer"});
        atomicabel.on("pointerdown", function(): void {
            window.open(atomic_link + "/profile/" + GameVars.userName);
        }, this);
        this.scaledItemsContainer.add(atomicabel);

        MainScene.currentInstance.mintedSound.play();
    }
}
