import { GameConstants } from "../GameConstants";
import { GameVars } from "../GameVars";
import { MainScene } from "./main-scene/MainScene";

export class EmbryoLayer extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene, isMainScene?: boolean) {

        super(scene);

        const tmpBackground = new Phaser.GameObjects.Graphics(this.scene);
        tmpBackground.fillStyle(0x0f1417, 1);
        tmpBackground.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        this.add(tmpBackground);

        const scaledItemsContainer = new Phaser.GameObjects.Container(this.scene);
        scaledItemsContainer.x = GameConstants.GAME_WIDTH / 2;
        scaledItemsContainer.y = GameConstants.GAME_HEIGHT / 2;
        scaledItemsContainer.scaleY = GameVars.scaleY;
        this.add(scaledItemsContainer);

        const embryo = new Phaser.GameObjects.Image(this.scene, 0, 0, "embryo");
        scaledItemsContainer.add(embryo);

        if (isMainScene) {

            this.scene.tweens.add({
                targets: this,
                alpha: 0,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 1000,
                delay: 500,
                onComplete: function(): void {
                    
                    MainScene.currentInstance.embryoLayer = null;
                    this.destroy();
                },
                onCompleteScope: this
            });
        }
    }
}
