export class GameVars {

    public static scaleY: number;
    public static isDesktop: boolean;
    public static aspectRatio: number;
    public static currentScene: Phaser.Scene;
    public static intitialised: boolean;
    public static language: string;
    public static reactClass: any;
    public static playerType: string;
    public static userName: string;
    public static assetID: number;
    public static characterIDs: any;
    public static currentAvatar: {};
    public static loading: boolean;
    public static interactButtons: boolean;
    public static waxAPI: any;
    public static autoShuffleDone: boolean;
  
    public static formatNumber(value: number): string {

        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
}
