import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { ButtonWithText } from "../../utils/ButtonWithText";
import { MainScene } from "./MainScene";

export class ConfirmLayer extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene) {

        super(scene);

        const tmpBackground = new Phaser.GameObjects.Graphics(this.scene);
        tmpBackground.fillStyle(0x000000, .5);
        tmpBackground.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        this.add(tmpBackground);

        const scaledItemsContainer = new Phaser.GameObjects.Container(this.scene);
        scaledItemsContainer.x = GameConstants.GAME_WIDTH * .5;
        scaledItemsContainer.y = GameConstants.GAME_HEIGHT * .5;
        scaledItemsContainer.scaleY = GameVars.scaleY;
        this.add(scaledItemsContainer);

        const bkgImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "box_small");
        scaledItemsContainer.add(bkgImage);
        
        const text = new Phaser.GameObjects.BitmapText(this.scene, 0, 0, "oswald-medium", "   ARE YOU SURE?\nYOU WANT TO MINT\n     THIS CITIZEN?", 42);
        text.setOrigin(.5);
        text.y = -50;
        scaledItemsContainer.add(text);

        const mintText = new Phaser.GameObjects.BitmapText(this.scene, 0, 0, "eunomia", "MINT", 42);
        mintText.tint = 0x0f1417;
        mintText.setOrigin(.5);
        const mintImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_regular");
        const mintButton = new ButtonWithText(this.scene, mintImage, mintText, this.onClickMint, this);
        mintButton.x = 120;
        mintButton.y = 100;
        scaledItemsContainer.add(mintButton);

        const cancelText = new Phaser.GameObjects.BitmapText(this.scene, 0, 0, "eunomia", "CANCEL", 42);
        cancelText.setOrigin(.5);
        cancelText.tint = 0x0f1417;
        const cancelImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_regular");
        const cancelButton = new ButtonWithText(this.scene, cancelImage, cancelText, this.onClickCancel, this);
        cancelButton.x = -120;
        cancelButton.y = 100;
        scaledItemsContainer.add(cancelButton);

        this.alpha = 0!;

        this.scene.tweens.add({
            targets: this,
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 1000, 
        });
        
    }

    private onClickMint(): void {

        MainScene.currentInstance.snapShot();
        GameManager.mintAvatar();
        MainScene.currentInstance.clickCategoryButtonSound.play();
    }

    private onClickCancel(): void {
        GameManager.returnToEditAvatar();
    }
}
