    public static mintAvatar(): void {

        let correctAvatar = IDsManager.checkAvatar(MainScene.currentInstance.getIsThinAvatar(), GameVars.playerType , GameVars.characterIDs);
        console.log("CORRECT AVATAR");
        console.log(correctAvatar);

        if (!GameVars.userName) {
            MainScene.currentInstance.showError("User name error. Please relogin and refresh the page.");
        }

        if (!GameVars.reactClass.state.wax || !GameVars.reactClass.state.wax.type) {
            MainScene.currentInstance.showError("Login error. Please relogin and refresh the page.");
        }

        if (correctAvatar.ret) {

            (async () => {

                let asset = await GameManager.getTableRows("avatarpacks", 1, 2, "i64", GameVars.userName, GameVars.userName, 0, "packsopenerx");
    
                let result;
    
                if (asset.rows.length && !asset.rows[0].claimable) {
    
                    try {
    
                        let actions = [{
                            account: "packsopenerx",
                            name: "claimavatar",
                            authorization: [{
                                actor: GameVars.reactClass.state.wax.userAccount,
                                permission: "active",
                            }],
                            data: {
                                unboxer: GameVars.reactClass.state.wax.userAccount,
                                pack_asset_id: GameVars.assetID,
                            }
                        }];
    
                        if (GameVars.reactClass.state.wax.type === "wcw") {
                            result = await GameVars.reactClass.state.wax.api.transact({
                                actions: actions
                            }, {
                                blocksBehind: 3,
                                expireSeconds: 30,
                            });
                        } else if (GameVars.reactClass.state.wax.type === "anchor") {
                            result = await GameVars.reactClass.state.wax.signTransaction({
                                actions: actions
                            }, {
                                blocksBehind: 3,
                                expireSeconds: 30,
                            });
                        }
                        
                    } catch (e) {
                        MainScene.currentInstance.showError(e.message, GameManager.onDuplicateDataErrorRemoved, GameManager);
                    }
                } else {
                    result = true;
                }
    
                if (result) {
                    fetch("/api/avatars/update-avatar", {
                        headers: {
                            "Content-type": "application/json"
                        },
                        method: "POST",
                        body: JSON.stringify({data: JSON.stringify(GameVars.characterIDs), username: GameVars.userName, pack_asset_id: GameVars.assetID, is_thin: MainScene.currentInstance.getIsThinAvatar()})
                    })
                    .then(res => res.json())
                    .then(data => {
                        console.log("AVATAR RETURN");
                        console.log(data);
                        MainScene.currentInstance.showInfoAvatarGenerated(data);
                    });
                }
                
            })();
        } else {
            MainScene.currentInstance.showError("Validator error with message:\n" + correctAvatar.error, GameManager.onDuplicateDataErrorRemoved, GameManager);
        }

        console.log(GameVars.characterIDs);
    }

