    public static rgbToHex(r: number, g: number, b: number): string {

        let rNum = Math.round(r * 255);
        let gNum = Math.round(g * 255);
        let bNum = Math.round(b * 255);
        
        return ((1 << 24) + (rNum << 16) + (gNum << 8) + bNum).toString(16).slice(1);
    }

