    public static writeGameData(): void {

        let dataCategory = {pleb: {}, ubernorm: {}, hiClone: {}, autoShuffleDone: false};

        dataCategory.autoShuffleDone = GameVars.autoShuffleDone;
        GameManager.getGameStorageData(
            GameConstants.SAVED_AVATAR_DATA_KEY,
            function (avatarData: string): void {

                if (avatarData) {
                
                    let d = JSON.parse(avatarData);

                    dataCategory.pleb = d.pleb;
                    dataCategory.ubernorm = d.ubernorm;
                    dataCategory.hiClone = d.hiClone;
                
                 }
            }
        );

        if (GameVars.playerType === "PLEB") {
            dataCategory.pleb = GameVars.currentAvatar;
        } else if (GameVars.playerType === "UBERNORM") {
            dataCategory.ubernorm = GameVars.currentAvatar;
        } else if (GameVars.playerType === "HI-CLONE") {
            dataCategory.hiClone = GameVars.currentAvatar;
        }

        GameManager.setGameStorageData(
            GameConstants.SAVED_AVATAR_DATA_KEY,
            dataCategory,
            function (): void {
                //
            }
        );
    }

    public static loadAvatarData(): void {
        GameManager.getGameStorageData(
            GameConstants.SAVED_AVATAR_DATA_KEY,
            function (avatarData: string): void {

                let loadData = false;
                if (avatarData) {
                    
                    let dataCategory = JSON.parse(avatarData);

                    if (GameVars.playerType === "PLEB" && dataCategory.pleb["HOLOGRAM"]) {
                        GameVars.currentAvatar = dataCategory.pleb;
                        loadData = true;
                    } else if (GameVars.playerType === "UBERNORM" && dataCategory.ubernorm["HOLOGRAM"]) {
                        GameVars.currentAvatar = dataCategory.ubernorm;
                        loadData = true;
                    } else if (GameVars.playerType === "HI-CLONE" && dataCategory.hiClone["HOLOGRAM"]) {
                        GameVars.currentAvatar = dataCategory.hiClone;
                        loadData = true;
                    }

                    if (dataCategory.autoShuffleDone && dataCategory.autoShuffleDone === true) {
                        GameVars.autoShuffleDone = dataCategory.autoShuffleDone;
                    }
                
                } 
                if (loadData) {
                    MainScene.currentInstance.readAvatarData();
                }  else {
                    MainScene.currentInstance.initAvatar();
                    MainScene.currentInstance.saveAvatarData();
                    GameManager.writeGameData();
                }
            }
        );

        GameVars.loading = false;
    }

    public static async getTableRows(table: string, limit: number, index_position: number, key_type: string, lower_bound: any, upper_bound: any, count: number, contract: string): Promise<any> {

        let JsonRpc;

        try {
            JsonRpc = require("eosjs").JsonRpc;
        } catch (e) {
            //
        } 

        if (JsonRpc) {
            let urls = ["https://query.3dkrender.com", "https://wax.pink.gg", "https://apiwax.3dkrender.com", "https://wax.eu.eosamsterdam.net", "https://api.waxsweden.org", "https://wax.blacklusion.io", "https://wax.csx.io", "https://wax.dapplica.io", "https://api-wax.eosarabia.net", "https://wax.eoseoul.io", "https://api.wax.greeneosio.com", "https://wax.hkeos.com", "https://api.wax.liquidstudios.io", "https://wax.eosn.io"];

            if (GameVars.reactClass && GameVars.reactClass.state && GameVars.reactClass.state.serverType === "testnet") {
                urls =  ["https://testnet.waxsweden.org"];
            }

            const rpc = new JsonRpc(urls[count]);
    
            try {
                return await rpc.get_table_rows({
                    json: true,                 
                    code: contract,       
                    scope: contract,      
                    table: table,  
                    limit: limit,
                    index_position: index_position,
                    key_type: key_type,
                    lower_bound: lower_bound,
                    upper_bound: upper_bound,                
                    reverse: false,             
                    show_payer: false,    
                });
            } catch (e) {
                if (count !== urls.length - 1) {
                    console.log("error" + urls[count]);
                    return GameManager.getTableRows(table, limit, index_position, key_type, lower_bound, upper_bound, count + 1, contract);
                }
            }
        }  
    }
    
    private static startGame(): void {

        GameVars.currentScene.scene.start("PreloadScene");
    }

    private static getGameStorageData(key: string, successCb: Function): void {

        const gameDataStr = localStorage.getItem(key);
        successCb(gameDataStr);
    }

    private static setGameStorageData(key: string, value: any, successCb: Function): void {

        localStorage.setItem(key, JSON.stringify(value));
        successCb();
    }
