import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { Button } from "../../utils/Button";
import { MainScene } from "./MainScene";

export class TopContainer extends Phaser.GameObjects.Container {

    private bodyParts: string[];
    private index: number;
    private title: Phaser.GameObjects.BitmapText;
    private leftBtnArrow: Button;
    private rightBtnArrow: Button;
    private arrowsPosition: Map<string, number>;
    private colorContainer: Phaser.GameObjects.Container;
    private colors: Phaser.GameObjects.Graphics[];
    private carruselContainer: Phaser.GameObjects.Container;
    private carrusel: Phaser.GameObjects.Image[];
    private leftArrow: Button;
    private rightArrow: Button;
    private selectedColorBackground: Phaser.GameObjects.Graphics;

    private enabledButtons: boolean;

    constructor(scene: Phaser.Scene, body: string[]) {

        super(scene);

        this.x = GameConstants.GAME_WIDTH / 2;
        this.bodyParts = body;
        this.index = 0;
        this.carruselContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.carruselContainer);
        this.carrusel = [];
        this.enabledButtons = false;

        // ave arrow map
        this.arrowsPosition = new Map([
            ["CAPE",                    550],
            ["BACK HAIR",               320],
            ["SOCKS",                   720],
            ["BOTTOM",                  590],
            ["SHOES",                   830],
            ["TOP",                     450],
            ["BELT",                    520],
            ["BODY TYPE",               480],
            ["JACKET",                  450],
            ["NECK",                    370],
            ["SLEEVES",                 450],
            ["MOUTH",                   360],
            ["FACIAL MARK",             320],
            ["FACE",                    320],
            ["NOSE",                    320],
            ["BEARD",                   380],
            ["EYEBROWS",                300],
            ["EYES",                    310],
            ["FRONT HAIR",              250],
            ["HOLOGRAM",                220],
            ["HEADPHONES",              250]
        ]);

        if (GameVars.autoShuffleDone) {
            this.enabledButtons = true;
        }

        this.selectedColorBackground = new Phaser.GameObjects.Graphics(this.scene);
        this.selectedColorBackground.fillStyle(0XFFFFFF);
        this.selectedColorBackground.fillRect(0, 0, 56, 56);
        this.selectedColorBackground.visible = false;
        this.add(this.selectedColorBackground);

        this.leftArrow = new Button(this.scene, -184, 50, "texture_atlas_1", "arrow_category", "arrow_category", true);
        this.leftArrow.onDown(() => {
            if (this.enabledButtons) {
                this.leftAction();
            }
        }, this);
        this.leftArrow.setInflationTween(1.1);
        this.add(this.leftArrow);

        this.rightArrow = new Button(this.scene, 184, 50, "texture_atlas_1", "arrow_category", "arrow_category", true);
        this.rightArrow.onDown(() => {
            if (this.enabledButtons) {
                this.rightAction();
            }
        }, this);
        this.rightArrow.setInflationTween(1.1, -1);
        this.rightArrow.scaleX = -1;
        this.add(this.rightArrow);

        this.title = new Phaser.GameObjects.BitmapText(this.scene, 0 , 52, "eunomia", this.bodyParts[this.index], 43);
        this.title.setOrigin(.5);
        this.add(this.title);

        this.leftBtnArrow = new Button(this.scene, -208, 0, "texture_atlas_1", "btn_arrow_item", "btn_arrow_item", true);
        this.leftBtnArrow.onDown(() => {
            if (this.enabledButtons) {
                this.changeToLeftItem();
            }
        }, this);
        this.leftBtnArrow.setInflationTween(1.1);
        this.add(this.leftBtnArrow);

        this.rightBtnArrow = new Button(this.scene, 208, 0, "texture_atlas_1", "btn_arrow_item", "btn_arrow_item", true);
        this.rightBtnArrow.onDown(() => {
            if (this.enabledButtons) {
                this.changeToRightItem();
            }
        }, this);
        this.rightBtnArrow.setInflationTween(1.1, -1);
        this.rightBtnArrow.scaleX = -1;
        this.add(this.rightBtnArrow);

        this.colorContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.colorContainer);

        this.rightAction();
    }

    public enableButtons(): void {
        this.enabledButtons = true;
    }

    public switchBody(): void {
        
        if (MainScene.currentInstance.getIsThinAvatar()) {

            if (this.bodyParts[this.index] === "JACKET") {
                this.leftAction();
            }

            let ind = MainScene.currentInstance.getCurrentAttachmentIndex("CAPE");
            if (this.bodyParts[this.index] === "CAPE" && ind > 7) {
                this.leftAction();
            }
        }
    }

    public onAvatarMinted(): void {

        this.leftArrow.visible = false;
        this.rightArrow.visible = false;
        
        this.leftBtnArrow.visible = false;
        this.rightBtnArrow.visible = false;
    }

    public onDuplicateDataErrorRemoved(): void {

        this.leftArrow.visible = true;
        this.rightArrow.visible = true;
        
        this.leftBtnArrow.visible = true;
        this.rightBtnArrow.visible = true;
    }

    public updateCarrussel() {

        const currentPos = MainScene.currentInstance.getCurrentAttachmentIndex(this.bodyParts[this.index]);
        for (let i = 0; i < this.carrusel.length; ++i) {
            if (i === currentPos) {
                this.carrusel[i].setScale(1.9);
            } else {
                this.carrusel[i].setScale(1);
            }
        }
    }

    public forceChangeColors(newColors: string): void {

        if (this.bodyParts[this.index] !== "MOUTH") {
            return;
        }

        switch (newColors) {
            case "hair":
                this.loadSlotColors("BEARD");
                break;

            case "skin":
                this.loadSlotColors("FACE");
                break;

            case "mouth":
                this.loadSlotColors("MOUTH");
                break;

            case "acc":
                this.loadSlotColors("HOLOGRAM");
                break; 

            case "nan":
                this.loadSlotColors("FACIAL MARK");
                break;

            default:
                break;
        }
    }

    private leftAction(): void {

        if (!GameVars.interactButtons) {
            return;
        }

        this.index--;

        if (this.index < 0) {
            this.index = this.bodyParts.length - 1;
        } 
        
        this.title.text = this.bodyParts[this.index];
        
        if ((MainScene.currentInstance.getIsThinAvatar() && this.bodyParts[this.index] === "JACKET") || this.bodyParts[this.index] === "arm_skin" ||
        this.bodyParts[this.index] === "flaps" || this.bodyParts[this.index] === "pelvis" || this.bodyParts[this.index] === "torso" || (this.bodyParts[this.index] === "HOLOGRAM" && GameVars.playerType !== "HI-CLONE")) {
            this.leftAction();
        }

        if (MainScene.currentInstance.getSlotAlpha("JACKET") === 0 && this.bodyParts[this.index] === "CAPE" && !MainScene.currentInstance.getIsThinAvatar()) {
            this.leftAction();
        }

        let ind = MainScene.currentInstance.getCurrentAttachmentIndex("TOP");
        if (this.bodyParts[this.index] === "CAPE" && ind > 7 && MainScene.currentInstance.getIsThinAvatar()) {
            this.leftAction();
        }

        if (this.bodyParts[this.index].includes("skin") || this.bodyParts[this.index].includes("_")) {
            this.leftAction();
        }

        if (GameVars.playerType === "PLEB" && (this.bodyParts[this.index] === "HEADPHONES" || this.bodyParts[this.index] === "FACIAL MARK")) {
            this.leftAction();
        }
        
        this.moveArrows();

        MainScene.currentInstance.clickCategoryButtonSound.play();
    }

    private rightAction(): void {

        if (!GameVars.interactButtons) {
            return;
        }
        
        this.index++;

        if (this.index > this.bodyParts.length - 1) {
            this.index = 0;
        }
        this.title.text = this.bodyParts[this.index];

        if ((MainScene.currentInstance.getIsThinAvatar() && this.bodyParts[this.index] === "JACKET") || this.bodyParts[this.index] === "arm_skin" ||
        this.bodyParts[this.index] === "flaps" || this.bodyParts[this.index] === "pelvis" || this.bodyParts[this.index] === "torso" || (this.bodyParts[this.index] === "HOLOGRAM" && GameVars.playerType !== "HI-CLONE")) {
            this.rightAction();
        }

        if (MainScene.currentInstance.getSlotAlpha("JACKET") === 0 && this.bodyParts[this.index] === "CAPE" && !MainScene.currentInstance.getIsThinAvatar()) {
            this.rightAction();
        }

        let ind = MainScene.currentInstance.getCurrentAttachmentIndex("TOP");
        if (this.bodyParts[this.index] === "CAPE" && ind > 7 && MainScene.currentInstance.getIsThinAvatar()) {
            this.rightAction();
        }

        if (this.bodyParts[this.index].includes("skin") || this.bodyParts[this.index].includes("_")) {
            this.rightAction();
        }

        if (GameVars.playerType === "PLEB" && (this.bodyParts[this.index] === "HEADPHONES" || this.bodyParts[this.index] === "FACIAL MARK")) {
            this.rightAction();
        }

        this.moveArrows();

        MainScene.currentInstance.clickCategoryButtonSound.play();
    }

    private moveArrows(): void {

        let newPos: number;

        if (this.arrowsPosition.has(this.bodyParts[this.index])) {

            newPos = this.arrowsPosition.get(this.bodyParts[this.index]);

            this.scene.tweens.add({
                targets: [this.leftBtnArrow, this.rightBtnArrow],
                y: newPos,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 250
            });
        }

        const currentPos = MainScene.currentInstance.getCurrentAttachmentIndex(this.bodyParts[this.index]);

        if (this.bodyParts[this.index] === "MOUTH" && (currentPos === 30 || currentPos === 31 || currentPos === 32 || currentPos === 33)) {
            this.loadSlotColors("BEARD");
        } else {
            this.loadSlotColors(this.bodyParts[this.index]);
        }
        this.loadCarrusel();
    }

    private changeToLeftItem(): void {

        if (!GameVars.interactButtons) {
            return;
        }

        if (this.bodyParts[this.index] === "BODY TYPE") {
            MainScene.currentInstance.switchBody();
        } else {
            MainScene.currentInstance.switchAttachment(this.bodyParts[this.index], true);
        }
        this.updateCarrussel();

        this.saveData();

        MainScene.currentInstance.clickButtonArrowSound.play();
    }

    private changeToRightItem(): void {

        if (!GameVars.interactButtons) {
            return;
        }

        if (this.bodyParts[this.index] === "BODY TYPE") {
            MainScene.currentInstance.switchBody();
        } else {
            MainScene.currentInstance.switchAttachment(this.bodyParts[this.index], false);
        }
        this.updateCarrussel();

        this.saveData();

        MainScene.currentInstance.clickButtonArrowSound.play();
    }
    
    private setColor(hexa: string): void {
        
        if (!GameVars.interactButtons) {
            return;
        }

        MainScene.currentInstance.changeColor(this.bodyParts[this.index], hexa);

        this.saveData();

        MainScene.currentInstance.clickButtonArrowSound.play();
    }

    private loadSlotColors(colorsType: string): void {

        this.selectedColorBackground.visible = false;
             
        if (this.colors) {

            this.colors.forEach(c => {
                c.destroy();
            });
        }

        if (colorsType === "EYEBROWS" || colorsType === "NOSE" || colorsType === "BODY TYPE") {
            return;
        }
        this.colors = [];

        let new_colors = MainScene.currentInstance.getColors(colorsType);
        
        for (let i = 0; i < new_colors.length; ++i) {
            
            const col = new_colors[i];
            const graphic = new Phaser.GameObjects.Graphics(this.scene);
            graphic.lineStyle(6, 0x000000);

            let grafCol = col;
            if (this.bodyParts[this.index] === "HOLOGRAM") {

                switch (col) {
                    case "49fff9":
                        grafCol = "27ffed";
                    break;

                    case "fff651":
                        grafCol = "aaff0b";
                    break;

                    case "fcff00":
                        grafCol = "ffdb00";
                    break;

                    case "f395ff":
                        grafCol = "ff24e9";
                    break;

                    default:
                        break;
                }
            }

            graphic.fillStyle(Number("0x" + grafCol));
            graphic.strokeRect(264, 120 + i * 60, 40, 40);
            
            graphic.setInteractive(new Phaser.Geom.Rectangle(264, 120 + i * 60, 40, 40), Phaser.Geom.Rectangle.Contains);

            graphic.on("pointerover", () => {

                this.selectedColorBackground.visible = true;

                this.selectedColorBackground.x = 264 - 8;
                this.selectedColorBackground.y = 120 + i * 60 - 8 + GameConstants.GAME_HEIGHT * .5 + 100 - new_colors.length * .5 * 100;

                this.sendToBack(this.selectedColorBackground);

            }, this);
    
            graphic.on("pointerout", () => {

                this.sendToBack(this.selectedColorBackground);

            }, this);
    
            graphic.on("pointerup", () => {

                this.setColor(col);

                this.selectedColorBackground.visible = true;

                this.selectedColorBackground.x = 264 - 8;
                this.selectedColorBackground.y = 120 + i * 60 - 8 + GameConstants.GAME_HEIGHT * .5 + 100 - new_colors.length * .5 * 100;

                this.sendToBack(this.selectedColorBackground);

            }, this);

            graphic.fillRect(264, 120 + i * 60, 40, 40);
            this.colors.push(graphic);
            this.colorContainer.add(graphic);
        }
        
        this.colorContainer.setPosition(0, GameConstants.GAME_HEIGHT * .5 + 100 -  this.colors.length * .5 * 100);
    }

    private loadCarrusel(): void {

        if (this.carrusel) {
            
            this.carrusel.forEach(c => {
                c.destroy();
            });
        }
        
        let new_carrussel = MainScene.currentInstance.getAssets(this.bodyParts[this.index], "basic");
        
        this.carrusel = [];
        let i = 0;

        if (this.bodyParts[this.index] === "CAPE" || this.bodyParts[this.index] === "BACK HAIR" || this.bodyParts[this.index] === "BELT" || this.bodyParts[this.index] ===  "JACKET" ||
        this.bodyParts[this.index] === "FACIAL MARK" || this.bodyParts[this.index] === "BEARD" || this.bodyParts[this.index] === "HOLOGRAM" || this.bodyParts[this.index] ===  "HEADPHONES"
        || this.bodyParts[this.index] ===  "SOCKS") {
            
            let img = new Phaser.GameObjects.Image(this.scene, i * 13, 0, "texture_atlas_1", "mark_void");
            this.carruselContainer.add(img);
            this.carrusel.push(img);

            ++i;
        }

        for (i; i < new_carrussel; ++i) {

            let img = new Phaser.GameObjects.Image(this.scene, i * 13, 0, "texture_atlas_1", "mark_pleb");
            this.carruselContainer.add(img);
            this.carrusel.push(img);
        }

        new_carrussel += MainScene.currentInstance.getAssets(this.bodyParts[this.index], "middle");
        for (i; i < new_carrussel; ++i) {

            let img = new Phaser.GameObjects.Image(this.scene, i * 13, 0, "texture_atlas_1", "mark_uber_normal");
            this.carruselContainer.add(img);
            this.carrusel.push(img);      
        }

        new_carrussel += MainScene.currentInstance.getAssets(this.bodyParts[this.index], "vip");
        for (i; i < new_carrussel; ++i) {

            let img = new Phaser.GameObjects.Image(this.scene, i * 13, 0, "texture_atlas_1", "mark_hi_clone");
            this.carruselContainer.add(img);
            this.carrusel.push(img);        
        }

        this.carruselContainer.setPosition(-this.carrusel.length * 0.5 * 13 + 5, 120);

        this.updateCarrussel();
    }

    private saveData(): void {
        MainScene.currentInstance.saveAvatarData();
        GameManager.writeGameData();
    }

    
}
