import { GameManager } from "../GameManager"; 
import { Game } from "../Game";
import { GameConstants } from "../GameConstants";
import { GameVars } from "../GameVars";

export class BootScene extends Phaser.Scene {

    public static currentInstance: BootScene;

    public static onOrientationChange(): void {

        BootScene.currentInstance.time.addEvent({ delay: 100, callback: () => {
            GameVars.scaleY = (GameConstants.GAME_WIDTH / GameConstants.GAME_HEIGHT) / (window.innerWidth / window.innerHeight);
            Game.currentInstance.scale.refresh();
        }, callbackScope: BootScene.currentInstance});
    }
    
    constructor() {

        super("BootScene");

        BootScene.currentInstance = this;
    }

    public preload(): void {

        this.load.image("embryo", "assets/embryo.png");   
        // this.load.image("tank", "assets/tank.png"); 
        this.load.bitmapFont("oswald", "assets/fonts/oswald.png", "assets/fonts/oswald.xml");
    }

    public create(): void {

        console.log("BOOT");
        
        GameManager.setCurrentScene(this);

        if (GameVars.currentScene.sys.game.device.os.desktop) {
            GameManager.init();
        } else {

            if (window.innerHeight < window.innerWidth) {
                document.getElementById("orientation").style.display = "block";
                document.getElementById("content").style.display = "none";
            } else {
                GameManager.init();
            }
        }
    }
}
