import { Game } from "phaser";
import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { ButtonWithText } from "../../utils/ButtonWithText";
import { MainScene } from "./MainScene";

export class BottomContainer extends Phaser.GameObjects.Container {

    private randButton: ButtonWithText;
    private mintButton: ButtonWithText;

    constructor(scene: Phaser.Scene) {

        super(scene);

        const mintImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_empty");
        mintImage.scaleX = -1;
        mintImage.setOrigin(.5);

        const mintText = new Phaser.GameObjects.BitmapText(this.scene, -50, 0, "eunomia", "MINT", 42);
        mintText.setOrigin(.5);

        this.mintButton = new ButtonWithText(this.scene, mintImage, mintText, this.mintFunction, this);
        this.mintButton.setPosition(GameConstants.GAME_WIDTH - 160, GameConstants.GAME_HEIGHT - 80);
        if (!GameVars.autoShuffleDone) {
            this.mintButton.enabled = false;
        }

        let iconImage = new Phaser.GameObjects.Image(this.scene, 100, 0, "texture_atlas_1", "icon_mint");
        iconImage.setOrigin(.5);
        this.mintButton.add(iconImage);
        this.add(this.mintButton);

        const randImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_empty");
        randImage.setOrigin(.5, .5);
        
        const randText = new Phaser.GameObjects.BitmapText(this.scene, 30, 0, "eunomia", "SHUFFLE", 42);
        randText.setOrigin(.5);

        this.randButton = new ButtonWithText(this.scene, randImage, randText, this.randFunction, this);
        this.randButton.setPosition( 160,  GameConstants.GAME_HEIGHT - 80);
        if (!GameVars.autoShuffleDone) {
            this.randButton.enabled = false;
        }

        iconImage = new Phaser.GameObjects.Image(this.scene, -100, -2, "texture_atlas_1", "icon_shuffle");
        iconImage.setOrigin(.5);

        this.randButton.add(iconImage);

        this.add(this.randButton);
    }

    public onAvatarMinted(): void {

        this.mintButton.visible = false;
        this.randButton.visible = false;
    }

    public onDuplicateDataErrorRemoved(): void {

        this.mintButton.visible = true;

        if (this.randButton) {
            this.randButton.visible = true;
        }
        
    }

    public onRandomAvatarReady(): void {

        this.randButton.alpha = 1;
    }

    public clickShuffleButtonAnim(): void {

        let handCursor = new Phaser.GameObjects.Image(this.scene, this.randButton.x + 210, this.randButton.y + 35, "texture_atlas_1", "hand");
        handCursor.setScale(.35);
        handCursor.alpha = 0;
        this.add(handCursor);

        this.scene.tweens.add({
            targets: handCursor,
            alpha: 1,
            scaleX: 1,
            scaleY: 1,
            ease: Phaser.Math.Easing.Elastic.Out,
            duration: 1000,
            delay: 1000,
            onComplete: () => {
                this.scene.tweens.add({
                    targets: handCursor,
                    x: handCursor.x - 50,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 1000,
                    delay: 500,
                    onComplete: () => {
                        this.scene.tweens.add({
                            targets: handCursor,
                            scaleX: 0.75,
                            scaleY: 0.75,
                            ease: Phaser.Math.Easing.Cubic.In,
                            duration: 250,
                            onComplete: () => {
                                this.randFunction();
                                this.scene.tweens.add({
                                    targets: handCursor,
                                    scaleX: 1.2,
                                    scaleY: 1.2,
                                    alpha: 0,
                                    ease: Phaser.Math.Easing.Cubic.In,
                                    duration: 1000,
                                    delay: 250,
                                    onComplete: () => {
                                        handCursor.destroy();
                                        this.randButton.enabled = true;
                                        this.mintButton.enabled = true;
                                        MainScene.currentInstance.enableButtons();
                                        GameManager.saveAutoShuffle();
                                    },
                                    onCompleteScope: this
                                });
                            },
                            onCompleteScope: this
                        });
                    },
                    onCompleteScope: this
                });
            },
            onCompleteScope: this
        });
    }

    private randFunction(): void {
       
        if (!GameVars.interactButtons) {
            return;
        }

        this.randButton.alpha = .4;

        GameManager.setInteractButtons(false);

        MainScene.currentInstance.shuffleAvatar();

        MainScene.currentInstance.clickCategoryButtonSound.play();

        MainScene.currentInstance.shuffleFXSound.play();
    }

    private mintFunction(): void {
       
        if (!GameVars.interactButtons) {
            return;
        }

        GameManager.setInteractButtons(false);
        MainScene.currentInstance.createConfirmLayer();
    }
}
