import { GameConstants } from "./../../GameConstants";
import { BottomContainer } from "./BottomContainer";
import { TopContainer } from "./TopContainer";
import { GameManager } from "../../GameManager";
import { EmbryoLayer } from "../EmbryoLayer";
import { AvatarGenerationLayer } from "./AvatarGenerationLayer";
import { ErrorLayer } from "../ErrorLayer";
import { AvatarContainer } from "./AvatarContainer";
import {data} from "../../../assets/characterData.json";
import {ConfirmLayer } from "./ConfirmLayer";
import { GameVars } from "../../GameVars";

export class MainScene extends Phaser.Scene {

    public static currentInstance: MainScene;

    public embryoLayer: EmbryoLayer;
    public clickButtonArrowSound: Phaser.Sound.BaseSound;
    public clickCategoryButtonSound: Phaser.Sound.BaseSound;
    public humSound: Phaser.Sound.BaseSound;
    public mintedSound: Phaser.Sound.BaseSound;
    public shuffleFXSound: Phaser.Sound.BaseSound;

    private t_container: TopContainer;
    private b_container: BottomContainer;
    private avatarContainer: AvatarContainer;
    private avatarGenerationLayer: AvatarGenerationLayer;
    private errorLayer: ErrorLayer;
    private confirmLayer: ConfirmLayer;

    constructor() {

        super("MainScene");
        
        MainScene.currentInstance = this;
    }

    public create(): void {

        this.embryoLayer = new EmbryoLayer(this, true);
        this.add.existing(this.embryoLayer);

        this.clickButtonArrowSound = this.sound.add("click_arrow");
        this.clickCategoryButtonSound = this.sound.add("click_categories_and_btns");
        this.humSound = this.sound.add("hum");
        this.mintedSound = this.sound.add("minted");
        this.shuffleFXSound = this.sound.add("shuffle_fx");
        // Check for NFT
        (async () => {

            let result;

            if (GameVars.userName) {
                result = await GameManager.getTableRows("avatarpacks", 1, 2, "i64", GameVars.userName, GameVars.userName, 0, "packsopenerx");

                if (result.rows.length) {
    
                    // set rarity
                    switch (result.rows[0].rarity) {
                        case "Pleb":
                            GameVars.playerType = "PLEB";
                            break;
                        case "UberNorm":
                            GameVars.playerType = "UBERNORM";
                            break;
                        case "Hi-Clone":
                            GameVars.playerType = "HI-CLONE";
                            break;
                        default:
                            GameVars.playerType = "PLEB";
                            break;
                    }
                    GameVars.assetID = parseInt(result.rows[0].pack_asset_id);
                }
            } else {
                GameVars.playerType = "PLEB";
            }

            this.avatarGenerationLayer = null;

            this.clickButtonArrowSound = this.sound.add("click_arrow");
            this.clickCategoryButtonSound = this.sound.add("click_categories_and_btns");
            this.humSound = this.sound.add("hum");

            const background = this.add.graphics();
            background.fillStyle(0x0f1417);
            background.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);

            // this.mintContainer = new Phaser.GameObjects.Container(this);
            // this.add.existing(this.mintContainer);

            let bkgAsset = new Phaser.GameObjects.Image(this, GameConstants.GAME_WIDTH * .5, GameConstants.GAME_HEIGHT * .5 + 20, "texture_atlas_1");

            let boxLights = new Phaser.GameObjects.Image(this, 385, GameConstants.GAME_HEIGHT * .5, "texture_atlas_1");  

            switch (GameVars.playerType) {
                case "PLEB":
                    bkgAsset.setFrame("bg_plebs");

                    boxLights.setFrame("frame_patch_plebs");

                    break;

                case "UBERNORM":
                    bkgAsset.setFrame("bg_ubernorm");

                    boxLights = null;
                    break;

                case "HI-CLONE":
                    bkgAsset.setFrame("bg_hi-clones");

                    boxLights.setFrame("frame_patch_hi-clones");

                    break;
                default:

                    bkgAsset.setFrame("bg_plebs");

                    boxLights.setFrame("frame_patch_plebs");
                    break;
            }

            bkgAsset.setScale(2);

            // this.mintContainer.add(bkgAsset);

            // if (boxLights !== null) {
            //     this.mintContainer.add(boxLights);
            //     this.mintContainer.bringToTop(boxLights);
            // }

            let time = Date.now();

            this.avatarContainer = new AvatarContainer(this, data);
            this.add.existing(this.avatarContainer);

            GameManager.loadAvatarData();

            this.t_container = new TopContainer(this, this.avatarContainer.getSlotsName());
            this.add.existing(this.t_container);

            this.b_container = new BottomContainer(this);
            this.add.existing(this.b_container);

            if (result && !result.rows.length) {
                this.showError("Staking a Citizen Amnio-Tank NFT is mandatory to use this editor.");
            } else if (!GameVars.reactClass.state.wax || !GameVars.reactClass.state.wax.type) {
                this.showError("Login error. Please relogin and refresh the page.");
            } else if (GameVars.reactClass.state.wax && GameVars.reactClass.state.wax.type === "wcw" && !GameVars.reactClass.state.wax.api) {
                this.showError("Login error. Please relogin and refresh the page.");
            } else if (!GameVars.userName) {
                this.showError("User name error. Please relogin and refresh the page.");
            } else {
                if (!GameVars.autoShuffleDone) {
                    this.b_container.clickShuffleButtonAnim();
                }
            }

        })();
    }

    public update(): void {

        if (this.avatarGenerationLayer) {
            
            this.avatarGenerationLayer.update();
        }
    }

    public initAvatar(): void {
        this.avatarContainer.initAvatar();
    }

    public switchBody(): void {

        this.avatarContainer.switchBody();
        this.t_container.switchBody();
    }
    
    
    public snapShot(): void {

        if (this.confirmLayer) {
            this.confirmLayer.destroy();
        }

        this.avatarContainer.createMap();

        this.t_container.onAvatarMinted();

        this.b_container.visible = false;

        this.avatarGenerationLayer = new AvatarGenerationLayer(this, GameVars.assetID);
        this.add.existing(this.avatarGenerationLayer);
    }

    public createConfirmLayer(): void {

        this.confirmLayer = new ConfirmLayer(this);
        this.add.existing(this.confirmLayer);
    }

    public returnToAvatarEditor(): void {
        this.tweens.add({
            targets: this.confirmLayer,
            alpha: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: () => {
                this.confirmLayer.destroy();
            } 
        });
    }

    public getIsThinAvatar(): boolean {
        return this.avatarContainer.getIsThinAvatar();
    }

    public switchAttachment(slot: any, left: boolean): void {
        this.avatarContainer.switchAttachment(slot, left);
    }

    public shuffleAvatar(): void {
        this.avatarContainer.shuffleAvatar();
    }

    public changeColor(slot: any, color: string): void {
        this.avatarContainer.changeColor(slot, color);
    }


    public forceToChangeColors(type: string): void {
        if (this.t_container) {
            this.t_container.forceChangeColors(type);
        }
    }

    public getSlotAlpha(slot: string): number {

        return this.avatarContainer.getSlotAlpha(slot);
    }

    public getColors(slot: string): string[] {

        return this.avatarContainer.getColors(slot);
    }

    public getAssets(slot: string, type: string): number {

        return this.avatarContainer.getAssets(slot, type);
    }

    public getCurrentAttachmentIndex(slot: string): number {

        return this.avatarContainer.getCurrentAttachmentIndex(slot);
    }

    public carrusselUpdate(): void {

        this.t_container.updateCarrussel();

        this.b_container.onRandomAvatarReady();
    }

    public showInfoAvatarGenerated(data: any): void {

        if (data.error) {

            this.showError(data.error, GameManager.onDuplicateDataErrorRemoved, GameManager);
            console.log(data);
        } else {

            console.log(data.result.value.processed.action_traces[0].inline_traces[0].inline_traces[0].act.data.asset_id);
            let asset_id = data.result.value.processed.action_traces[0].inline_traces[0].inline_traces[0].act.data.asset_id;

            this.avatarGenerationLayer.onNFTAvailable(parseInt(asset_id));

            // console.log(data);
        }
    }

    public saveAvatarData(): void {

        this.avatarContainer.saveAvatarData();
    }

    public readAvatarData(): void {
        this.avatarContainer.readAvatarData();
    }

    public enableButtons(): void {
        this.t_container.enableButtons();
    }

    public showError(error: string, retryFunction?: Function, retryFunctionContext?: any): void {

        this.errorLayer = new ErrorLayer(this, "ERROR", error, retryFunction, retryFunctionContext);
        this.add.existing(this.errorLayer);
    }

    public onDuplicateDataErrorRemoved(): void {

        this.b_container.visible = true;

        this.t_container.onDuplicateDataErrorRemoved();
        this.b_container.onDuplicateDataErrorRemoved();

        if (this.avatarGenerationLayer) {
            this.avatarGenerationLayer.destroy();
        }
    }
}
