import { GameConstants } from "../GameConstants";
import { ButtonWithText } from "../utils/ButtonWithText";

export class ErrorLayer extends Phaser.GameObjects.Container {

    private scaledItemsContainer: Phaser.GameObjects.Container;
    private retryFunction: Function;
    private retryFunctionContext: any;

    constructor(scene: Phaser.Scene, errorType: string, errorMessage: string, retryFunction?: Function, retryFunctionContext?: any) {
        
        super(scene);

        this.retryFunction = retryFunction;
        this.retryFunctionContext = retryFunctionContext;

        const background = new Phaser.GameObjects.Graphics(this.scene);
        background.fillStyle(0x91D35, .85);
        background.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        background.setInteractive(new Phaser.Geom.Rectangle(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT), Phaser.Geom.Rectangle.Contains);
        this.add(background);

        this.scaledItemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.scaledItemsContainer.x = GameConstants.GAME_WIDTH * .5;
        this.scaledItemsContainer.y = GameConstants.GAME_HEIGHT * .4;
        this.add(this.scaledItemsContainer);

        const menuBackground = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "box_small");
        this.scaledItemsContainer.add(menuBackground);

        const duelLogo = new Phaser.GameObjects.Image(this.scene, 0, -175, "texture_atlas_1", "logo_cd");
        this.scaledItemsContainer.add(duelLogo); 

        const titleLabel = new Phaser.GameObjects.BitmapText(this.scene, 0, -38, "oswald-medium", errorType, 40);
        titleLabel.setOrigin(.5);
        this.scaledItemsContainer.add(titleLabel);

        if (errorMessage.length > 120) {
            errorMessage = errorMessage.slice(0, 120);
            errorMessage += "...";
        }

        const errorMessageLabel = new Phaser.GameObjects.BitmapText(this.scene, 0, 55, "oswald", errorMessage, 30);
        errorMessageLabel.setOrigin(.5);
        errorMessageLabel.tint = 0x00ddfe;
        errorMessageLabel.align = 1;
        errorMessageLabel.setMaxWidth(460);
        this.scaledItemsContainer.add(errorMessageLabel);

        if (this.retryFunction) {

            const tryAgainButtonImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_regular");
            const tryAgainButtonLabel = new Phaser.GameObjects.BitmapText(this.scene, 0, 0, "oswald-medium", "TRY AGAIN", 36);
            tryAgainButtonLabel.setOrigin(.5);
            tryAgainButtonLabel.tint = 0x361601;
    
            const tryAgainButton = new ButtonWithText(this.scene, tryAgainButtonImage, tryAgainButtonLabel, this.onTryAgainDown, this);
            tryAgainButton.y = 230;
            this.scaledItemsContainer.add(tryAgainButton);
        }
    }

    private onTryAgainDown(): void {

        if (this.retryFunction) {
            this.retryFunction.apply(this.retryFunctionContext);
        }

        this.destroy();
    }
}
