import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { MainScene } from "./MainScene";

export class AvatarContainer extends Phaser.GameObjects.Container {

    private characterData: any;   
    private character: any;
    private isThin: boolean;

    constructor(scene: Phaser.Scene, characterData: any) {
        super(scene);

        this.isThin = true;

        let bkgAsset = new Phaser.GameObjects.Image(this.scene, GameConstants.GAME_WIDTH * .5, GameConstants.GAME_HEIGHT * .5 + 20, "texture_atlas_1");

        let boxLights = new Phaser.GameObjects.Image(this.scene, 385, GameConstants.GAME_HEIGHT * .5, "texture_atlas_1");  

        switch (GameVars.playerType) {
            case "PLEB":
                bkgAsset.setFrame("bg_plebs");

                boxLights.setFrame("frame_patch_plebs");

                break;

            case "UBERNORM":
                bkgAsset.setFrame("bg_ubernorm");

                boxLights = null;
                break;

            case "HI-CLONE":
                bkgAsset.setFrame("bg_hi-clones");

                boxLights.setFrame("frame_patch_hi-clones");

                break;
            default:
                break;
        }

        bkgAsset.setScale(2);

        this.add(bkgAsset);

        const frame = new Phaser.GameObjects.Image(this.scene, GameConstants.GAME_WIDTH * .5, GameConstants.GAME_HEIGHT * .5, "texture_atlas_1", "frame_uber");
        this.add(frame);

        if (boxLights !== null) {
            this.add(boxLights);
            this.bringToTop(boxLights);
        }
        this.characterData = characterData;

        this.character = (this as any).scene.add.spine(0, 0, "avatar");
        this.character.skeleton.setToSetupPose();
        this.character.setSkinByName("default");  
        this.add(this.character);

        this.character.x = GameConstants.GAME_WIDTH * .5;
        this.character.y = GameConstants.GAME_HEIGHT * .5 + 380;

        this.character.play("idle", true, true);

        this.hideSlot("JACKET");

        if ((this.isThin && this.getCurrentAttachmentIndex("TOP") > 7) || (!this.isThin && this.getSlotAlpha("JACKET") === 0)) {
            this.hideSlot("CAPE");
        }

     }

     public initAvatar(): void {

        this.isThin = true;

        this.hideSlot("JACKET");
        this.hideSlot("CAPE");

        if (GameVars.playerType !== "HI-CLONE") {
            this.hideSlot("HOLOGRAM");
        }

        if (GameVars.playerType === "PLEB") {
            this.hideSlot("HEADPHONES");
            this.hideSlot("FACIAL MARK");
        }

        let s = this.character.skeleton.findSlot("facialmark_fill");
        s.color.a = 0;

        this.drawCharacter();
     }

     public switchAttachment(slotName: string, left: boolean): void {

        let currentSlot = this.characterData[slotName];

        let index = this.character.skeleton.findSlotIndex(currentSlot.line);

        // array of assets
        let arr = [];
        for (let s in this.character.skeleton.skin.attachments[index]) {
            if (s) {
                arr.push(s);
            }
        } 

        let index2;

        if (currentSlot.line === "facialmark") {

            index2 = this.character.skeleton.findSlotIndex(currentSlot.fill);

            for (let s in this.character.skeleton.skin.attachments[index2]) {
                if (s) {
                    arr.push(s);
                }
            } 
        }
        
        let assetsIds = [];

        if (this.isThin) {
            assetsIds = currentSlot.basicThinAssets;
            if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThinAssets);
                if (GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThinAssets);
                }
            }
        } else {
            assetsIds = currentSlot.basicThickAssets;
            if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThickAssets);
                if (GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThickAssets);
                }
            }
        }
        
        let newIndex = 0;
        if (assetsIds) {
            newIndex = assetsIds[0];
        }

        for (let j = 0; j < assetsIds.length; ++j) {

            if (currentSlot.line === "facialmark") {
                if (j < currentSlot.numAssets) {    
                    newIndex = this.searchAttachmentIndex(assetsIds, index, arr, j, left);
                    
                } else {
                    newIndex = this.searchAttachmentIndex(assetsIds, index2, arr, j, left);
                }
            
            } else {
                newIndex = this.searchAttachmentIndex(assetsIds, index, arr, j, left); 
            }
            if (newIndex !== -1) {
                    break;
            }
        }

        if (newIndex === -1) {
            newIndex = assetsIds[0];
        }

        if (currentSlot.line === "facialmark") {
            this.character.skeleton.slots[index2].data.attachmentName = arr[newIndex];
            this.character.skeleton.slots[index].data.attachmentName = arr[newIndex];

            if (newIndex < currentSlot.numAssets) {

                let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                s.color.a = 1;
                s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                s.color.a = 0;

                this.setCharacterAttachment(this.characterData[slotName].line, arr[newIndex]);

            } else {

                let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                s.color.a = 0;
               
                s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                s.color.a = 1;
                this.setCharacterAttachment(this.characterData[slotName].fill, arr[newIndex]);
               
            }

        } else {

            this.character.skeleton.slots[index].data.attachmentName = arr[newIndex];
            
            this.setCharacterAttachment(this.characterData[slotName].line, arr[newIndex]);

            
            this.changeFill(arr, newIndex, this.characterData[slotName]);
            
            this.changeSecondSlot(arr, newIndex, this.characterData[slotName]);
        }

        if (slotName === "TOP" && this.isThin && newIndex > 7) {
            this.hideSlot("CAPE");
        } else if (slotName === "TOP" && this.isThin) {
            this.showSlot("CAPE");
        }

        this.specificChanges(this.characterData[slotName], arr[newIndex], newIndex, null);
    }

    public shuffleAvatar(): void {
        let arr = [];
        for (let s in this.characterData) {
            if (s) {
                if (s !== "BODY TYPE") {
                arr.push(s);
                }
            }
        }
        
        // rand thin o thick body
        let rand = Math.floor(Math.random() * 2);

        if ((!this.isThin && rand === 0) || (this.isThin && rand === 1)) {
            MainScene.currentInstance.switchBody();
        }

        this.isThin = rand === 0;

        GameVars.currentAvatar["isThin"] = this.isThin;

        for (let i = 0; i < arr.length; ++i) {

            const slotName = arr[i];
            const currentSlot = this.characterData[slotName];

            if ((this.isThin && slotName === "JACKET") || slotName === "arm_skin" || slotName === "flaps" ||
            slotName === "pelvis" || slotName === "torso" || (slotName === "HOLOGRAM" && GameVars.playerType !== "HI-CLONE")) {
            continue;
        }

            // get Assets
            let assetsIds = [];

            if (this.isThin) {
                assetsIds = currentSlot.basicThinAssets;
                if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.middleThinAssets);
                    if (GameVars.playerType === "HI-CLONE") {
                        assetsIds = assetsIds.concat(currentSlot.vipThinAssets);
                    }
                }
            } else {
                assetsIds = currentSlot.basicThickAssets;
                if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.middleThickAssets);
                    if (GameVars.playerType === "HI-CLONE") {
                        assetsIds = assetsIds.concat(currentSlot.vipThickAssets);
                    }
                }
            }

            const newAssetId = Math.floor(Math.random() * assetsIds.length);
        
            // get Colors
            let colors = [];
            if (slotName === "MOUTH" && (newAssetId === 30 || newAssetId === 31 || newAssetId === 32 || newAssetId === 33)) {
                
                colors = colors.concat("BEARD");
                if (GameVars.playerType === "HI-CLONE") {
                    colors = colors.concat("BEARD");
                }
            } else {

                colors = colors.concat(currentSlot.colors);
                if (GameVars.playerType === "HI-CLONE") {
                    colors = colors.concat(currentSlot.vipColors);
                }
            }

            let newColorId;
            if (!colors || colors.length === 0 || slotName === "EYEBROWS" || slotName === "NOSE") {
                GameManager.saveCurrentAvatar(slotName, assetsIds[newAssetId], null);

            } else {
                newColorId  = Math.floor(Math.random() * colors.length);
                
                GameManager.saveCurrentAvatar(slotName, assetsIds[newAssetId], colors[newColorId].hexa);
            }
        }

        this.readAvatarData().then(() => {

            MainScene.currentInstance.carrusselUpdate();
            GameManager.setInteractButtons(true);

            MainScene.currentInstance.saveAvatarData();
            GameManager.writeGameData();
        });
    }


    public async readAvatarData(): Promise<void> {

        const arr = [];

        for (let s in GameVars.currentAvatar) {
            if (s) {
                arr.push(s);
            }
        }

        if (this.isThin  !== GameVars.currentAvatar["isThin"]) {
            this.switchBody();
        }

        for (let i = 0; i < arr.length; ++i) {
            
            const slotName = arr[i];

            if (slotName === "isThin") {
                continue;
            }

            const currentData = GameVars.currentAvatar[slotName];
            const currentSlot = this.characterData[slotName];

            if (slotName === "neck_skin" || slotName === "bottom_back" || slotName === "leg_skin") {
                continue;
            }

            let index = this.character.skeleton.findSlotIndex(currentSlot.line);
            let index2 = this.character.skeleton.findSlotIndex(currentSlot.fill);

            let attachments = [];

            for (let att in this.character.skeleton.skin.attachments[index]) {
                if (att) {
                    attachments.push(att);
                }
            }

            if (slotName === "FACIAL MARK") {
                for (let att in this.character.skeleton.skin.attachments[index2]) {
                    if (att) {
                        attachments.push(att);
                    }
                }
            }

            if (slotName === "FACIAL MARK") {
                this.character.skeleton.slots[index2].data.attachmentName = arr[currentData.id];
                this.character.skeleton.slots[index].data.attachmentName = arr[currentData.id];
    
                if (currentData.id < currentSlot.numAssets) {
    
                    let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                    s.color.a = 1;
                    s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                    s.color.a = 0;
    
                    this.setCharacterAttachment(this.characterData[slotName].line, attachments[currentData.id]);
    
                } else {
    
                    let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                    s.color.a = 0;
                   
                    s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                    s.color.a = 1;
                    this.setCharacterAttachment(this.characterData[slotName].fill, attachments[currentData.id]);
                   
                }
    
            } else {
    
                this.character.skeleton.slots[index].data.attachmentName = attachments[currentData.id];
                
                this.setCharacterAttachment(this.characterData[slotName].line, attachments[currentData.id]);
    
                this.changeFill(attachments, currentData.id, this.characterData[slotName]);
                
                this.changeSecondSlot(attachments, currentData.id, this.characterData[slotName]);
            }
    
            
            if (currentSlot.fill || slotName === "HOLOGRAM") {
                this.changeColor(slotName, currentData.color);
                
                if (this.isThin && slotName === "JACKET") {
                    this.hideSlot(slotName);
                }

                if (this.isThin && slotName === "CAPE" && GameVars.currentAvatar["TOP"].id > 7) {
                    this.hideSlot(slotName);
                }

            }

            this.specificChanges(this.characterData[slotName], attachments[currentData.id], currentData.id, currentData.color);  
            
            if (!GameVars.loading) {
                await this.delay(100);
            }
        }
    }

    public getColors(slot: string): string[] {

        const arr: string [] = [];
        this.characterData[slot].colors.forEach(col => {
            arr.push(col.hexa);
        });
        if (GameVars.playerType === "HI-CLONE") {
            this.characterData[slot].vipColors.forEach(col => {
                arr.push(col.hexa);
            });
        }
        return arr;
    }

    public getIsThinAvatar(): boolean {

        return this.isThin;
    }

    public changeColor(slot: string, color: string): void {

        if (slot === "HOLOGRAM") {
            this.setSlotColor(this.characterData[slot].line, slot, color);
        } else if (slot === "MOUTH") {
            let index = this.character.skeleton.findSlotIndex(this.characterData[slot].line);

            let s = this.character.skeleton.slots[index];
            let attName: string = s.getAttachment().name;

            // array of assets
            let i = 0;
            for (let s in this.character.skeleton.skin.attachments[index]) {
                if (s === attName) {
                   break;
                } else { 
                    ++i;
                }
            } 

            this.specificChanges(this.characterData[slot], attName, i, color);
        } else {
            this.setSlotColor(this.characterData[slot].fill, slot, color);
            if (this.characterData[slot].assossiations.fill) {
                this.setSlotColor(this.characterData[slot].assossiations.fill, slot, color);
            }

            let index = this.character.skeleton.findSlotIndex(this.characterData[slot].line);

            let s = this.character.skeleton.slots[index];
            if (s.getAttachment()) {
                this.specificChanges(this.characterData[slot], s.getAttachment().name, null, color);
            }
        }
    }

    public getAssets(slot: string, type: string): number {

        if (this.isThin) {
           if (type === "basic") {
                return this.characterData[slot].basicThinAssets.length;
           } else if (type === "middle") {
                return this.characterData[slot].middleThinAssets.length;
           } else if (type === "vip") {
                return this.characterData[slot].vipThinAssets.length;
           }
                        
        } else {
            if (type === "basic") {
                return this.characterData[slot].basicThickAssets.length;
           } else if (type === "middle") {
                return this.characterData[slot].middleThickAssets.length;
           } else if (type === "vip") {
                return this.characterData[slot].vipThickAssets.length;
           }
        }
    }

    public getCurrentAttachmentIndex(slotName: string): number {

        if (slotName === "BODY TYPE") {
            return this.isThin ? 0 : 1;
        }
        let currentSlot = this.characterData[slotName];

        let index = this.character.skeleton.findSlotIndex(currentSlot.line);

        let i = 0;

        for (let s in this.character.skeleton.skin.attachments[index]) {
            if (s === this.character.skeleton.slots[index].data.attachmentName) {
                break;
            } else {
                ++i;
            }
        } 
        index = this.character.skeleton.findSlotIndex(currentSlot.fill);

        if (currentSlot.line === "facialmark" && i !== 0) {
            for (let s in this.character.skeleton.skin.attachments[index]) {
                if (s === this.character.skeleton.slots[index].data.attachmentName) {
                    break;
                } else {
                    ++i;
                }
            }
        }

        let assetsIds = [];

        if (this.isThin) {
            assetsIds = currentSlot.basicThinAssets;
            if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThinAssets);
                if (GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThinAssets);
                }
            }
        } else {
            assetsIds = currentSlot.basicThickAssets;
            if (GameVars.playerType === "UBERNORM" || GameVars.playerType === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThickAssets);
                if (GameVars.playerType === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThickAssets);
                }
            }
        }

        for (let j = 0; j < assetsIds.length; ++j) {
            if (assetsIds[j] === i) {
                return j;
            }
        }

        return 0;
    }

    public getSlotAlpha(slot: string, index?: number): number {
        let currentSlot = this.characterData[slot];
        let s = 1;
        if (currentSlot.line === "facialmark") {
            if (index < currentSlot.numAssets ) {
                s = this.character.skeleton.findSlot(this.characterData[slot].line).color.a;
            } else {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill).color.a;
            }
        } else {
            if (currentSlot.line) {
                s = this.character.skeleton.findSlot(this.characterData[slot].line).color.a;
            } else if (currentSlot.fill) {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill).color.a;
            }
        }
        return s;
    }

    public getSlotsName(): string[] {

        let arr = [];
        for (let s in this.characterData) {
            if (s) {
                arr.push(s);
            }
        }   
        return arr;
    }

    public switchBody(): void {

        this.isThin = !this.isThin;

        if (this.isThin) {
            this.hideSlot("JACKET");
            this.hideSlot("CAPE");
        } else {
            this.showSlot("JACKET");
            this.showSlot("CAPE");
        }

        this.switchAttachment("arm_skin", true);
        this.switchAttachment("SLEEVES", true);
        this.switchAttachment("torso", true);
        this.switchAttachment("TOP", true);
        this.switchAttachment("NECK", true);
    }

    public createMap(): void {
        
        const arr = [];

        for (let s in this.characterData) {
            if (s) {
                if (s !== "BODY TYPE") {
                arr.push(s);
                }
            }
        }

        let cdIter = -1;
        
        for (let element = 0; element < arr.length; ++element) {
            cdIter++;            
            let value = this.characterData[arr[element]];

            let index = this.character.skeleton.findSlotIndex(value.line);
            let index_fill = this.character.skeleton.findSlotIndex(value.fill);

            if ((arr[element] === "HEADPHONES" || arr[element] === "FACIAL MARK") && GameVars.playerType === "PLEB") {
                GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: null, color: null};
                continue;
            }

            // array of assets
            let i = 0;

            let ind = index;
            if (arr[cdIter] === "arm_skin" || arr[cdIter] === "neck_skin") {
                ind = index_fill;
            }

            let findID = false;
            for (let s in this.character.skeleton.skin.attachments[ind]) {
                if (s) {
                    i++;
                    if (s === this.character.skeleton.slots[ind].data.attachmentName) {
                        findID = true;
                        break;
                    }
                }
            }

            if (arr[cdIter] === "FACIAL MARK" && !findID) {
                for (let s in this.character.skeleton.skin.attachments[index_fill]) {
                    if (s) {
                        i++;
                        if (s === this.character.skeleton.slots[index_fill].data.attachmentName) {
                            findID = true;
                            break;
                        }
                    }
                }
            }

            if (i === -1) {
                console.log("in slot " + arr[cdIter] + "attribute not found");
                continue;
            } 

            if (!findID) {
                GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: null, color: null};
                continue;
            }

            let stringIndex;
            if (i < 10) {
                stringIndex = "0" + (i).toString();
            } else {
                stringIndex = (i).toString();
            }

            if (arr[cdIter] === "arm_skin") {
                if (stringIndex === "01") {
                    stringIndex = "thick_1";
                } else if (stringIndex === "03") {
                    stringIndex = "thin";
                } else {
                    stringIndex = "thick_2";
                }
            } else if (arr[cdIter] === "neck_skin") {
                if (stringIndex === "01") {
                    stringIndex = "thick";
                } else {
                    stringIndex = "thin";
                }
            } else if (arr[cdIter] === "bottom_back") {
                if (i <= 10) {
                    GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: null, color: null};
                    continue;
                }
             }

            if (arr[cdIter] === "leg_skin" || arr[cdIter] === "pelvis") {
                stringIndex = "";
            }

            let color;
            if (this.character.skeleton.slots[index_fill]) {
                 color = this.character.skeleton.slots[index_fill].color;
            }

            if (arr[cdIter] === "HOLOGRAM" || arr[cdIter] === "EAR" || arr[cdIter] === "pelvis") {
                color =  this.character.skeleton.slots[index].color;
            }

            if (arr[cdIter] === "MOUTH") {
                if (i === 31 || i === 32 || i === 33 || i === 34) {
                    index = this.character.skeleton.findSlotIndex("moustache_fill");
                    color = this.character.skeleton.slots[index].color;
                } else {
                    index = this.character.skeleton.findSlotIndex("mouth_fill");
                    color = this.character.skeleton.slots[index].color;
                }
            }

            if ((!color && arr[cdIter] !== "MOUTH") || (arr[cdIter] === "FACIAL MARK" && stringIndex === "01")) {
                GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: stringIndex, color: null};
                continue;
            }

            // no slot
            if (color.a < 1 && arr[cdIter] !== "MOUTH") {
                GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: null, color: null};
                continue;
            }

            const hex = GameManager.rgbToHex(color.r, color.g, color.b);

            let colors = [];
            if (arr[cdIter] === "MOUTH" && (i === 31 || i === 32 || i === 33 || i === 34)) {
               
                colors = colors.concat(this.characterData["BEARD"].colors);
            
                if (GameVars.playerType === "HI-CLONE") {
                    colors = colors.concat(this.characterData["BEARD"].vipColors);
                }
            
            } else {

                colors = colors.concat(value.colors);
                
                if (GameVars.playerType === "HI-CLONE") {
                    colors = colors.concat(value.vipColors);
                }
            }

            if (!colors || colors.length === 0 || !hex) {
                GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: stringIndex, color: null};
                continue;
            }

            let name = null;
            for (let j = 0; j < colors.length; ++j) {
                if ( hex === colors[j].hexa) {
                    name = colors[j].name;
                    break;
                }
            }
           
            GameVars.characterIDs[GameManager.namesforServer[arr[cdIter]]] = {id: stringIndex, color: name};
       }  
       
    //    console.log(GameVars.characterIDs);
    }
    
    public saveAvatarData(): void {
        let arr = [];
        for (let s in this.characterData) {
            if (s) {
                if (s !== "BODY TYPE") {
                arr.push(s);
                }
            }
        }

        GameVars.currentAvatar["isThin"] = this.isThin;

        if (GameVars.playerType !== "HI-CLONE") {
            GameManager.saveCurrentAvatar("HOLOGRAM", null, null);
        } 
        
        if (GameVars.playerType === "PLEB") {
            GameManager.saveCurrentAvatar("HEADPHONES", null, null);
            GameManager.saveCurrentAvatar("FACIAL MARK", null, null);
        }

        for (let i = 0; i < arr.length; ++i) {

            const slotName = arr[i];
            const currentSlot = this.characterData[slotName];

            if ((this.isThin && slotName === "JACKET") || slotName === "arm_skin" || slotName === "flaps" ||
            slotName === "pelvis" || slotName === "torso" || (slotName === "HOLOGRAM" && GameVars.playerType !== "HI-CLONE")) {
                continue;
            }

            if ((slotName === "HEADPHONES" || slotName === "FACIAL MARK") && GameVars.playerType === "PLEB") {
                continue;
            }
            
            let index = this.character.skeleton.findSlotIndex(currentSlot.line);
            let index2 = this.character.skeleton.findSlotIndex(currentSlot.fill);


            // array of assets
            let newAssetId = 0;
            for (let attachment in this.character.skeleton.skin.attachments[index]) {
                if (attachment === this.character.skeleton.slots[index].data.attachmentName) {
                    break;
                } else {
                    newAssetId++;
                }
            }

            if (slotName === "FACIAL MARK" && i !== 0) {
                for (let attachment in this.character.skeleton.skin.attachments[index2]) {
                    if (attachment === this.character.skeleton.slots[index2].data.attachmentName) {
                        break;
                    } else {
                        newAssetId++;
                    }
                }
            } 

            let c = this.character.skeleton.slots[index].color;

            if (slotName === "MOUTH" && (newAssetId === 30 || newAssetId === 31 || newAssetId === 32 || newAssetId === 33)) {
                index2 = this.character.skeleton.findSlotIndex("moustache_fill");
            }

            if (currentSlot.fill) {
                c = this.character.skeleton.slots[index2].color;
            }

            if (c && (slotName !== "EYEBROWS" ||  slotName === "NOSE")) {
                let newColor = GameManager.rgbToHex(c.r, c.g, c.b);
            
                GameManager.saveCurrentAvatar(slotName, newAssetId, newColor);
                
            } else {
                GameManager.saveCurrentAvatar(slotName, newAssetId, null);
            }
        }
    }

    private hideSlot(slot: string): void {

        let currentSlot = this.characterData[slot];
        let s;

        if (currentSlot.line) {
            s = this.character.skeleton.findSlot(this.characterData[slot].line);
            s.color.a = 0;
        }

        if (currentSlot.fill) {
            s = this.character.skeleton.findSlot(this.characterData[slot].fill);
            s.color.a = 0;
        }

        if (currentSlot.assossiations.name) {            
            s = this.character.skeleton.findSlot(currentSlot.assossiations.name);
            s.color.a = 0;
        }

        if (currentSlot.assossiations.fill) {            
            s = this.character.skeleton.findSlot(currentSlot.assossiations.fill);
            s.color.a = 0;
        }
    }

    private showSlot(slot: string, index?: number): void {
        let currentSlot = this.characterData[slot];
        let s;

        if (currentSlot.line === "facialmark") {
            
            if (index < currentSlot.numAssets) {
               if (currentSlot.line) {
                s = this.character.skeleton.findSlot(this.characterData[slot].line);
                s.color.a = 1;
            }
            } else {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill);
                s.color.a = 1;       
            }
        } else {
            if (currentSlot.line) {
                s = this.character.skeleton.findSlot(this.characterData[slot].line);
                s.color.a = 1;
            }
    
            if (currentSlot.fill) {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill);
                s.color.a = 1;
            }
    
            if (currentSlot.assossiations.name) {            
                s = this.character.skeleton.findSlot(currentSlot.assossiations.name);
                s.color.a = 1;
            }
    
            if (currentSlot.assossiations.fill) {            
                s = this.character.skeleton.findSlot(currentSlot.assossiations.fill);
                s.color.a = 1;
            }
        }
    }

    private delay(ms: number): Promise <any> {

        return new Promise( resolve => setTimeout(resolve, ms) );
    }
    
    private setSlotColor(slot: string, parent: string, hexa: string): string {

        // 0 < r, g, b > 1
        let s: any;
        s = this.character.skeleton.findSlot(slot);
        
        let arr = this.characterData[parent].colors;
        arr = arr.concat(this.characterData[parent].vipColors);

        let name: string;
        let i = 0;
        for (i ; i < arr.length; ++i) {
            if (hexa === arr[i].hexa) {
                name = arr[i].name;
                break;
            } else if (i === arr.length - 1) {
                return null;
            }
        }
        let isInvisble = false;
        if (s.color.a === 0) {
            isInvisble = true;
        }
        if (slot === "accesory") {
            if (s) {

                s.color.setFromString(hexa);

                let dColor = this.characterData[parent].darkColor[i];

                s.darkColor.setFromString(dColor.hexa);
            }
        } else {
            if (s) {
                s.color.setFromString(hexa);  
            }
        }

        if (isInvisble) {
            s.color.a = 0;
        }

        return name;
    }

    private specificChanges(slot: any, currentAtt?: string, currentId?: number, col?: string): void {
        
        if (slot.specificChanges.length > 0) {

            slot.specificChanges.forEach(c => {
                
                switch (c.type) {
                    case "forceTogether":
                        
                        let color = JSON.parse(JSON.stringify(this.character.skeleton.findSlot(slot.fill).color));
                        this.forceTogether(c, currentAtt, color);
                        break;

                    case "changeColor":

                        let s = this.character.skeleton.findSlot(c.slot);
                        let ref = this.character.skeleton.findSlot(c.ref);

                        s.color = JSON.parse(JSON.stringify(ref.color));
                        break;

                    case "changeColorCategory":

                        let isInArray = false;

                        for (let i = 0; i < c.ids.length; ++i) {
                            if (c.ids[i] === currentId) {
                                isInArray = true;
                                break;
                            }
                        }

                        if (isInArray) {
                            if (currentAtt !== null) {
                                this.setCharacterAttachment(c.newLine, currentAtt);
                                this.setCharacterAttachment(c.newFill, currentAtt + "_fill");
                            }

                            let index = this.character.skeleton.findSlotIndex(slot.line);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(c.newLine);
                            this.character.skeleton.slots[index].color.a = 1;

                            index = this.character.skeleton.findSlotIndex(slot.fill);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(c.newFill);
                            if (col) {
                                this.character.skeleton.slots[index].color.setFromString(col);
                            } else {
                                this.character.skeleton.slots[index].color.a = 1;
                            }

                           MainScene.currentInstance.forceToChangeColors(c.colorType);
                        } else {
                            let index = this.character.skeleton.findSlotIndex(slot.line);
                            this.character.skeleton.slots[index].color.a = 1;

                            index = this.character.skeleton.findSlotIndex(c.newLine);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(slot.fill);
                             if (col) {
                                this.character.skeleton.slots[index].color.setFromString(col);
                            } else {
                                this.character.skeleton.slots[index].color.a = 1;
                            }

                            index = this.character.skeleton.findSlotIndex(c.newFill);
                            this.character.skeleton.slots[index].color.a = 0;
                            
                            MainScene.currentInstance.forceToChangeColors(slot.colorType);
                        }
                        break;

                    default:
                        console.log("no exist this type!!");
                        break;
                    
                }
            });
        }
    }

    private forceTogether(c: any, currentAtt: string, color: any): void {
        let isInArray = false;
        let i = 0;
        for (i; i < c.att.length; ++i) {
            if (c.att[i] === currentAtt) {
                isInArray = true;
                break;
            }
        }
        if (c.parnerSlot === "arm_skin_fill") {
            if (!isInArray && !this.isThin) {
                isInArray = true;
                i = 3;
            }
        }
        if (!isInArray && c.inv) {
            let s = this.character.skeleton.findSlot(c.parnerSlot);
            s.color.a = 0;

            s = this.character.skeleton.findSlot(c.parnerSlotFill);

            if (s) {
                s.color.a = 0;
            }
        } else if (!isInArray && !c.inv) {

            if (c.parnerSlot === "torso" && this.isThin) {

                this.setCharacterAttachment(c.parnerSlot, "q_torso/torso_02");
                
                this.setCharacterAttachment(c.parnerSlotFill, "q_torso/torso_02_fill");
            }
  
        } else if (isInArray) {
            let s = this.character.skeleton.findSlot(c.parnerSlot);
            s.color.a = 1;

            this.setCharacterAttachment(c.parnerSlot, c.parner[i]);

            if (c.parnerSlotFill) {
                s = this.character.skeleton.findSlot(c.parnerSlotFill);

                this.setCharacterAttachment(c.parnerSlotFill, c.parnerFill[i]);
            }
        }
    }

    private changeFill(arr: any[], rand: number, currentSlot: any): void {

        if (currentSlot.fill && arr[rand]) {
            let fillSlot = arr[rand] + "_fill";     
            this.setCharacterAttachment(currentSlot.fill, fillSlot);
        } else if (currentSlot.fill) {
            this.setCharacterAttachment(currentSlot.fill, arr[rand]);
        }
    }

    private changeSecondSlot(arr: any[], rand: number, currentSlot: any): void {
        
        if (!arr[rand] && currentSlot.assossiations.name) {
            this.setCharacterAttachment(currentSlot.assossiations.fill, arr[rand]);
            this.setCharacterAttachment(currentSlot.assossiations.name, arr[rand]);

            return; 
        }
        if (currentSlot.assossiations.name) {            
            let arr_a = [];
            // change sprites
            let index_a = this.character.skeleton.findSlotIndex(currentSlot.assossiations.name);
            for (let s in this.character.skeleton.skin.attachments[index_a]) {
                if (s) {
                    arr_a.push(s);
                }
            }

            this.setCharacterAttachment(currentSlot.assossiations.name, arr_a[rand]);

            
            if (currentSlot.assossiations.fill) {                
                let p = arr[rand].indexOf("/lines/");
                let fillSlot = arr[rand];
                if (p !== -1) {
                    fillSlot = arr[rand].substr(0, p + 1) + "fills" + arr[rand].substr(p + 6, arr[rand].length);
                }

                this.setCharacterAttachment(currentSlot.assossiations.fill, fillSlot + "_fill"); 
            }
        }         
    }

    private setCharacterAttachment(slot: string, name: string): void {       

        this.character.skeleton.setAttachment(slot, name);

        let index = this.character.skeleton.findSlotIndex(slot);

        this.character.skeleton.slots[index].data.attachmentName = name;
    }

    private searchAttachmentIndex(assetsIds: any, index: number, arr: any, j: number, left: boolean): number {

        if (this.character.skeleton.slots[index].data.attachmentName === arr[assetsIds[j]]) {
            if (left) {
                if (j === 0) {
                    j = assetsIds.length;
                }
                return assetsIds[j - 1];
            } else {
                if (j > assetsIds.length - 2) {
                    j = -1;
                }
               return assetsIds[j + 1];
            }
        } 
        return -1;
    }

    private drawCharacter(): void {

        let cData = [];
        for (let s in this.characterData) {
            if (s) {
                if (s === "EYEBROWS" || s === "NOSE" || s === "arm_skin" || s === "leg_skin" || s === "torso" || s === "neck_skin" || s === "pelvis" || s === "flaps" || s === "BODY TYPE" ) { 
                    continue;
                }
                cData.push(s);
            }
        }
        
        for (let i = 0; i < cData.length; ++i) {
            
            let index = this.character.skeleton.findSlotIndex(this.characterData[cData[i]].line);
            
            if (!this.character.skeleton.slots[index].color) {
                continue;
            }

            if (!this.characterData[cData[i]].colors || this.characterData[cData[i]].colors.length === 0) {
                continue;
            }

            if (cData[i].fill) {
                this.changeColor(cData[i], this.characterData[cData[i]].colors[0].hexa);
            } else {
                this.changeColor(cData[i], this.characterData[cData[i]].colors[0].hexa);

            }
        }
    }
}
