import Phaser from "phaser";
import { GameConstants } from "../GameConstants";
import { GameManager } from "../GameManager";
import { GameVars } from "../GameVars";
import { EmbryoLayer } from "./EmbryoLayer";

export class PreloadScene extends Phaser.Scene {

    public static currentInstance: PreloadScene;

    private background: Phaser.GameObjects.Graphics;

    private scaledItemsContainer: Phaser.GameObjects.Container;
    private progressBarContainer: Phaser.GameObjects.Graphics;
    private progressBar: Phaser.GameObjects.Graphics;
    private embryoLayer: EmbryoLayer;

    private loadingLabel: Phaser.GameObjects.BitmapText;

    constructor() {

        super("PreloadScene");

        PreloadScene.currentInstance = this;
    }
    
    public preload(): void {

        GameManager.setCurrentScene(this);

        this.composeScene();

        (this.load as any).spine("avatar", "assets/spine/avatars.json", ["assets/spine/avatars.atlas"], true);
        this.load.atlas("texture_atlas_1", "assets/texture_atlas_1.png", "assets/texture_atlas_1.json");   

        this.load.bitmapFont("eunomia", "assets/fonts/eunomia.png", "assets/fonts/eunomia.xml");
        this.load.bitmapFont("oswald-medium", "assets/fonts/Oswald-Medium.png", "assets/fonts/Oswald-Medium.xml");

        this.load.on("progress", this.updateLoadedPercentage, this);

        this.load.image("bg_plebs", "assets/bg_plebs.jpg");   
        this.load.image("bg_ubernorm", "assets/bg_ubernorm.jpg");  
        this.load.image("bg_hi-clones", "assets/bg_hi-clones.jpg");    

        // EL AUDIO
        this.load.audio("click_arrow", ["assets/audio/click_arrow.mp3", "assets/audio/click_arrow.ogg"]);
        this.load.audio("click_categories_and_btns", ["assets/audio/click_categories_and_btns.mp3", "assets/audio/click_categories_and_btns.ogg"]);
        this.load.audio("hum", ["assets/audio/hum.mp3", "assets/audio/hum.ogg"]);
        this.load.audio("minted", ["assets/audio/minted.mp3", "assets/audio/minted.ogg"]);
        this.load.audio("shuffle_fx", ["assets/audio/shuffle_fx.mp3", "assets/audio/shuffle_fx.ogg"]);
    }

    public create(): void {

        setTimeout(() => {
            this.loadingLabel.setText("CLICK TO CONTINUE");
            this.progressBarContainer.visible = false;
            this.progressBar.visible = false;
    
            this.background.setInteractive(new Phaser.Geom.Rectangle(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT), Phaser.Geom.Rectangle.Contains);
            this.background.on("pointerdown", () => {
                this.background.input.enabled = false;
                GameManager.onGameAssetsLoaded();
            }, this);
    
            this.tweens.add({
                targets: this.loadingLabel,
                alpha: .5,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 1000,
                yoyo: true,
                repeat: -1
            });
        }, 2000);

        
    }

    public update(): void {

        this.embryoLayer.update();
    }
    
    private composeScene(): void {

        this.background = this.add.graphics();
        this.background.fillStyle(0x0f1417);
        this.background.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);

        this.embryoLayer = new EmbryoLayer(this, false);
        this.add.existing(this.embryoLayer);

        this.scaledItemsContainer = this.add.container();
        this.scaledItemsContainer.x = GameConstants.GAME_WIDTH / 2;
        this.scaledItemsContainer.y = GameConstants.GAME_HEIGHT / 2;
        this.scaledItemsContainer.scaleY = GameVars.scaleY;

        this.loadingLabel = new Phaser.GameObjects.BitmapText(this, 0, 250, "oswald", "LOADING ...", 43);
        this.loadingLabel.setOrigin(.5);
        this.loadingLabel.tint = 0x82d6f8;
        this.scaledItemsContainer.add(this.loadingLabel);

        this.progressBarContainer = new Phaser.GameObjects.Graphics(this);
        this.progressBarContainer.fillStyle(0x2c3c93);
        this.progressBarContainer.fillRect(0, 0, 360, 5);
        this.progressBarContainer.x = -180;
        this.progressBarContainer.y = 285;
        this.scaledItemsContainer.add(this.progressBarContainer);
        
        this.progressBar = new Phaser.GameObjects.Graphics(this);
        this.progressBar.fillStyle(0x05d6ff);
        this.progressBar.fillRect(0, 0, 360, 5);
        this.progressBar.x = -180;
        this.progressBar.y = 285;
        this.progressBar.scaleX = 0;
        this.scaledItemsContainer.add(this.progressBar);
    }

    private updateLoadedPercentage(percentageLoaded: number): void {
        
        this.progressBar.scaleX = percentageLoaded;
    }
}
