export class ButtonWithText extends Phaser.GameObjects.Container {

    public enabled: boolean = true;
    
    private originalScale:  number;

    constructor(scene: Phaser.Scene, buttonImage: Phaser.GameObjects.Image, label: Phaser.GameObjects.BitmapText, callback: Function, context: any) {
        
        super(scene);

        buttonImage.setInteractive({ useHandCursor: true });

        buttonImage.on("pointerover", () => { 
            if (this.enabled) {
                this.originalScale = this.scaleX;
                this.setScale(1.1 * this.originalScale);  
            } 
        }, this);
        
        buttonImage.on("pointerout", () => { 
            if (this.enabled) {
                this.setScale(this.originalScale); 
            } 
        }, this);

        buttonImage.on("pointerup", () => {
            if (this.enabled) {
                callback.apply(context);
            }
        }, this);
        this.add(buttonImage);

        this.add(label);
    }
}
